import React from "react";
import { graphql } from "gatsby";
import { AuthorContent } from "../../components/Author/AuthorContent";
import { IPost, ICategory, IAuthor, IQueryArrayResult, IGatsbyLocation } from '../../interfaces';
import { ContentLayoutWithSidebar } from "../../components/Layout/ContentLayout";
import { SideBar } from "../../components/SideBar/SideBar";
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import { CategoryBar } from "../../components/CategoryBar/CategoryBar";
import { PostList } from "../../components/Post/PostList";
import { urlJoin, nestCategories } from '../../tool';
import { IHeaderProps } from "../../components/Layout/Header";

interface IQueryResult 
{
  data: {
    posts: IQueryArrayResult<IPost>;
    categories: IQueryArrayResult<ICategory>;
    avatar: any;
  }
  context: {
    author: IAuthor;
  }
  location: IGatsbyLocation;
}
export class AuthorPage extends React.Component<IQueryResult>
{
  render() 
  {
    let posts      = this.props.data.posts.edges
      .map(e => e.node)
      .sort((a,b) => {
        if(a.fields.timestamp > b.fields.timestamp)
          return -1
        else
          return 1;
      });

    let categories = this.props.data.categories.edges.map(e => e.node);

    let header: IHeaderProps = {
      url: urlJoin("author", this.props.context.author.frontmatter.slug),
      description: `Posts von ${this.props.context.author.frontmatter.firstname + " " + this.props.context.author.frontmatter.lastname}`,
      type: "object",
      title: `${this.props.context.author.frontmatter.firstname + " " + this.props.context.author.frontmatter.lastname}, Autor bei Powerpivot Insights | Durchblick durch Daten`,
      tags: ["author", this.props.context.author.frontmatter.firstname, this.props.context.author.frontmatter.lastname],
      image: this.props.data.avatar.childImageSharp.fixed.src
    }
    return(
      <ContentLayoutWithSidebar 
        header={header}
        sidebar={(
          <SideBar isFixed={false}>
            <SideBarQuickstart/>
            <SideBarHelp/>
            {/* <SideBarCategories/> */}
          </SideBar> 
        )} 
        >
      <CategoryBar title={"Author"}/>
      <AuthorContent author={this.props.context.author} avatar={this.props.data.avatar}></AuthorContent>
      <hr/>
      <PostList posts={posts} categories={nestCategories(categories)} authors={[this.props.context.author]}/>
      </ContentLayoutWithSidebar>
    )
  }
}


const Page = ({data, pageContext, location}) => {
  return (
    <AuthorPage data={data} context={pageContext} location={location}></AuthorPage>
  );
};

export default Page;

export const pageQuery = graphql`
  query authorPosts($authorSlug: String!, $avatar: String!, $now: Int!) {
    posts: allMarkdownRemark(
      filter: {
        fileAbsolutePath: {
          regex: "/.*\/data\/posts\/.*[.]md/"
        }
        frontmatter: {
          author: {
            eq: $authorSlug
          }
        }
        fields: {
          publishTimestamp:{
            lt: $now
          }
        }
      }
    )
    {
      edges {
        node {
          ...postShort
        }
      }
    }
    avatar: file(
    	absolutePath: { regex: "/.*\/data\/img\//"}  
      name:{ eq: $avatar}
    ){
      childImageSharp {
        fixed(width: 100, height: 100) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    ...categories
  }
`;
